:root {
    --rotation-rate: 3500ms;
  }

.container{
    display: block;
    justify-content: center;
    align-content: center;
}

.truck-container{
    display: block;
    justify-content: center;
    align-content: center;
}

.wheel{
    width: 110px;
    position: relative;
    top: 440px;

    -webkit-animation:spin var(--rotation-rate) infinite linear;
    -moz-animation:spin var(--rotation-rate) infinite linear;
    -ms-animation:spin var(--rotation-rate) infinite linear;
    animation:spin var(--rotation-rate) infinite linear;
}

#wheel-left{
    margin-right: 122px;
    margin-left: 8px;
}

#wheel-right{
    margin-left: 130px;
}

@-webkit-keyframes spin {
    100%{ -webkit-transform: rotate(360deg); }
}
@-moz-keyframes spin {
    100%{ -moz-transform: rotate(360deg); }
}
@-ms-keyframes spin {
    100%{ -ms-transform: rotate(360deg); }
}
@keyframes spin {
    100%{ transform: rotate(360deg); }
}

.loader{
    margin: 50px auto 10px;
    width: 500px;
    height: 25px;
    border-radius: 14px;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #999;
    background: #ccc;
    overflow: hidden;
    position: relative;
  }

  